.appointmentcalendar4 * {
  padding: 0px;
  margin: 0px;
}

.appointmentcalendar4 {
  /* margin-top: 150px; */
  padding: 20px;
}

.appointmentcalendar4 .showdate {
  display: inline-flex;
  width: 466px;
  background: #4267b2;
  color: white;
  padding: 8px 200px 8px 200px;
}

.appointmentcalendar4 .selected {
  background: rgb(44,147,85);
  color: white;
  width: 360px;
  padding: 8px;
  display: inline-block;
  height: 48px;
}

.appointmentcalendar4 .selected h1 {
  font-size: 20px;
  line-height: 20px;
  display: block;
}

.appointmentcalendar4 .selected h2{
  margin-top: 10px;
  font-size: 18px;
  line-height: 18px;
  display: block;
}

.appointmentcalendar4 .showdate .avatarsize {
  width: 30px;
  height: 30px;
  margin-top: 5px;
}

.appointmentcalendar4 .showdate .iconsize {
  font-size: 30px;
  color: #4267b2;
  font-weight: 900;
}

.appointmentcalendar4 .showdate h1 {
    font-size: 35px;
    font-weight: 700;
    line-height: 35px;
    text-align: center;
    width: 100%;
}

.appointmentcalendar4 .calendar {
  overflow: hidden;
  margin: auto;
  display: table;
}

.appointmentcalendar4 .calendar th h1 {
  font-weight: bold;
  font-size: 30px;
  text-align: left;
  color: #185875;
}

.appointmentcalendar4 .calendar td {
  font-weight: normal;
  font-size: 15px;
  -webkit-box-shadow: 0 2px 2px -2px #0E1119;
  -moz-box-shadow: 0 2px 2px -2px #0E1119;
  box-shadow: 0 2px 2px -2px #0E1119;
  /* font-weight: 900; */
  color: black;
  width: 120px;
  height: 100px;
}

.appointmentcalendar4 .day{
  position: relative;
  width: 120px;
  height: 100px;
  border: 1px solid black;
}

.appointmentcalendar4 .day h3 {
  position: absolute;
  width: 120px;
  height: 100px;
  line-height: 15px;
  text-align: left;
  font-weight: 900;
}

.appointmentcalendar4 .day h3:hover{
  cursor: pointer;
}

.appointmentcalendar4 .day .diagonal {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 120px;
  height: 100px;
}

.appointmentcalendar4 .day .bordertop {
  border-top: 1px solid black;
}

.appointmentcalendar4 .day .bold {
  font-size: 25px;
  font-weight: 900;
  line-height: 20px;
  color: lightcoral;
}

.appointmentcalendar4 .day .diagonal .line{
  stroke:black;
  stroke-width: 1;
}

.appointmentcalendar4 .day .day_first {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 120px;
  height: 50px;
  border-bottom: 1px solid black;
}

.appointmentcalendar4 .day .day_second {
  position: absolute;
  bottom: 0px;
  left: 0px;
  width: 120px;
  height: 50px;
}

.appointmentcalendar4 .day .clickday {
  position: absolute;
  left: 0px;
  top: 0px;
  width: 116px;
  height: 96px;
  background: rgba(255,255,0, 0.6);
  border: 2px solid purple;
}

.appointmentcalendar4 .avatarimage {
  width: 25px;
  height: 25px;
  margin-left: 15px;
  margin-top: 15px;
  float: left;
}

.appointmentcalendar4 .content{
  float: left;
  margin-top: 5px;
  margin-left: 5px;
  width: 60px;
  text-align: left;
  color: black;
}

.appointmentcalendar4 .content p {
  font-size: 12px;
  line-height: 12px;
  font-weight: 700;
}

.appointmentcalendar4 .day .blue {
  background: url("./pending.png") 105px 10px no-repeat;
}

.appointmentcalendar4 .day .green {
  background: url("./confirmed.png") 105px 10px no-repeat;
}

.appointmentcalendar4 .day .red {
  background: url("./declined.png") 105px 10px no-repeat;
}

.appointmentcalendar4 .day .orange {
  background: url("./requesttoconfirm.png") 100px 10px no-repeat;
}

.appointmentcalendar4 .calendar td,.appointmentcalendar4 .calendar th {
  text-align: center;
  width: 120px;
}

.appointmentcalendar4 .calendar th h1 {
  text-align: center;
  color: white;
}

/* Background-color of the odd rows */
.appointmentcalendar4 .calendar tr:nth-child(odd) {
	  background-color: rgba(222,222,222,0.7);
}

/* Background-color of the even rows */
.appointmentcalendar4 .calendar tr:nth-child(even) {
	  background-color: rgba(200,200,200,0.7);
}

.appointmentcalendar4 .calendar th {
	  background-color: #1F2739;
}

.appointmentcalendar4 .calendar th:first-child h1 { color: #FB667A; }
.appointmentcalendar4 .calendar td:first-child { color: #FB667A; }

.appointmentcalendar4 .calendar th:last-child h1 { color: #FB667A; }
.appointmentcalendar4 .calendar td:last-child { color: #FB667A; }

