* {
  padding: 0px;
  margin: 0px;
}

.appointmentcalendar1 {
  padding: 20px;
}

.appointmentcalendar1 .showdate {
  display: inline-block;
  background: #4267b2;
  width: 360px;
  text-align: center;
  padding: 8px;
}

.appointmentcalendar1 .showdate h1 {
    font-size: 2em; 
    font-weight: 700;
    line-height:0.8em;
    text-align: center;
    color: white;
    /* width: 300px; */
}

.appointmentcalendar1 .calendar {
  overflow: hidden;
  margin: 0 auto;
  display: table;
}

.appointmentcalendar1 .calendar th h1 {
  font-weight: bold;
  font-size: 20px;
  text-align: left;
  color: #185875;
  margin: 0px;
}

.appointmentcalendar1 .calendar td {
  font-weight: normal;
  font-size: 15px;
  -webkit-box-shadow: 0 2px 2px -2px #0E1119;
  -moz-box-shadow: 0 2px 2px -2px #0E1119;
  box-shadow: 0 2px 2px -2px #0E1119;
  font-weight: 800;
  color: black;
  width: 50px;
  height: 50px;
}

.appointmentcalendar1 .day{
  position: relative;
  width: 50px;
  height: 50px;
  border: 1px solid black;
}

.appointmentcalendar1 .day h3 {
  position: absolute;
  width: 50px;
  height: 50px;
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.appointmentcalendar1 .day .diagonal {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 50px;
  height: 50px;
}

.appointmentcalendar1 .day .bordertop {
  border-top: 1px solid black;
}

.appointmentcalendar1 .day .bold {
  font-size: 25px;
  font-weight: 900;
  color: lightcoral;
}

.appointmentcalendar1 .day .diagonal .line{
  stroke:black;
  stroke-width: 1;
}

.appointmentcalendar1 .day .day_first {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 50px;
  height: 25px;
}

.appointmentcalendar1 .day .day_second {
  position: absolute;
  bottom: 0px;
  left: 0px;
  width: 50px;
  height: 25px;
}

.appointmentcalendar1 .day .blue {
  background-color: rgb(0,100,255);
}

.appointmentcalendar1 .day .green {
  background-color: green;
}

.appointmentcalendar1 .day .red {
  background-color: red;
}

.appointmentcalendar1 .day  .orange {
  background-color: orange;
}

.appointmentcalendar1 .calendar td,.appointmentcalendar1 .calendar th {
  text-align: center;
  width: 50px;
}

.appointmentcalendar1 .calendar th h1 {
  text-align: center;
  color: white;
}

/* Background-color of the odd rows */
.appointmentcalendar1 .calendar tr:nth-child(odd) {
	  background-color: rgba(222,222,222,0.7);
}

/* Background-color of the even rows */
.appointmentcalendar1 .calendar tr:nth-child(even) {
	  background-color: rgba(200,200,200,0.7);
}

.appointmentcalendar1 .calendar th {
	  background-color: #1F2739;
}

.appointmentcalendar1 .calendar th:first-child h1 { color: #FB667A; }
.appointmentcalendar1 .calendar td:first-child { color: #FB667A; }

.appointmentcalendar1 .calendar th:last-child h1 { color: #FB667A; }
.appointmentcalendar1 .calendar td:last-child { color: #FB667A; }
