* {
  padding: 0px;
  margin: 0px;
}

.appointmentcalendar2 {
  padding: 20px;
}

.appointmentcalendar2 .showdate {
  display: inline-block;
  width: 514px;
  background: #4267b2;
  padding: 8px;
}

.appointmentcalendar2 .showdate h1 {
    font-size:2.5em; 
    font-weight: 700;
    line-height:0.8em;
    text-align: center;
    color: white;
    width: 100%
}

.appointmentcalendar2 .typename {
  display: inline-block;
  width: 514px;
  padding: 8px;
  background: #4267b2;
  color: white;
}

.appointmentcalendar2 .calendar {
  overflow: hidden;
  margin: auto;
  display: table;
}

.appointmentcalendar2 .calendar th h1 {
  font-weight: bold;
  font-size: 1.5em;
  text-align: left;
  color: #185875;
}

.appointmentcalendar2 .calendar td {
  font-weight: normal;
  font-size: 20px;
  -webkit-box-shadow: 0 2px 2px -2px #0E1119;
  -moz-box-shadow: 0 2px 2px -2px #0E1119;
  box-shadow: 0 2px 2px -2px #0E1119;
  font-weight: 800;
  color: black;
  width: 70;
  height: 70px;
}

.appointmentcalendar2 .day{
  position: relative;
  width: 70px;
  height: 70px;
  border: 2px solid black;
}

.appointmentcalendar2 .day h3 {
  position: absolute;
  width: 70px;
  height: 70px;
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.appointmentcalendar2 .day h3:hover {
  cursor: pointer;
}

.appointmentcalendar2 .day .diagonal {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 70px;
  height: 70px;
}

.appointmentcalendar2 .day .bordertop {
  border-top: 2px solid black;
}

.appointmentcalendar2 .day .diagonal .line{
  stroke:black;
  stroke-width: 2;
}

.appointmentcalendar2 .day .day_first {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 70px;
  height: 35px;
}

.appointmentcalendar2 .day .day_second {
  position: absolute;
  bottom: 0px;
  left: 0px;
  width: 70px;
  height: 35px;
}

.appointmentcalendar2 .day .clickday {
  position: absolute;
  left: 0px;
  top: 0px;
  width: 70px;
  height: 70px;
  background: rgba(255,255,0, 0.6);
}

.appointmentcalendar2 .day .blue {
  background-color: rgb(0,100,255);
}

.appointmentcalendar2 .day .green {
  background-color: green;
}

.appointmentcalendar2 .day .red {
  background-color: red;
}

.appointmentcalendar2 .day .orange {
  background-color: orange;
}

.appointmentcalendar2 .day .bold {
  font-size: 25px;
  font-weight: 900;
  color: lightcoral;
}
.appointmentcalendar2 .calendar td,.appointmentcalendar2 .calendar th {
  text-align: center;
  width: 70px;
}

.appointmentcalendar2 .calendar th h1 {
  text-align: center;
  color: white;
}

/* Background-color of the odd rows */
.appointmentcalendar2 .calendar tr:nth-child(odd) {
	  background-color: rgba(222,222,222,0.7);
}

/* Background-color of the even rows */
.appointmentcalendar2 .calendar tr:nth-child(even) {
	  background-color: rgba(200,200,200,0.7);
}

.appointmentcalendar2 .calendar th {
	  background-color: #1F2739;
}

.appointmentcalendar2 .calendar th:first-child h1 { color: #FB667A; }
.appointmentcalendar2 .calendar td:first-child { color: #FB667A; }

.appointmentcalendar2 .calendar th:last-child h1 { color: #FB667A; }
.appointmentcalendar2 .calendar td:last-child { color: #FB667A; }