.appointmentcalendar3 * {
  padding: 0px;
  margin: 0px;
}

.appointmentcalendar3 {
  /* margin-top: 150px; */
  padding: 20px;
}

.appointmentcalendar3 .showdate {
  display: inline-flex;
  width: 360px;
  background: #4267b2;
  color: white;
  padding: 8px;
}

.appointmentcalendar3 .selected {
  background: rgb(44,147,85);
  color: white;
  width: 360px;
  padding: 8px;
  display: inline-block;
  height: 48px;
}

.appointmentcalendar3 .selected h1 {
  font-size: 20px;
  line-height: 20px;
  display: block;
}

.appointmentcalendar3 .selected h2{
  margin-top: 10px;
  font-size: 18px;
  line-height: 18px;
  display: block;
}

.appointmentcalendar3 .showdate .avatarsize {
  width: 30px;
  height: 30px;
  margin-top: 5px;
}

.appointmentcalendar3 .showdate .iconsize {
  font-size: 30px;
  color: #4267b2;
  font-weight: 900;
}

.appointmentcalendar3 .showdate h1 {
    font-size: 35px;
    font-weight: 700;
    line-height: 35px;
    text-align: center;
    width: 100%
}



.appointmentcalendar3 .calendar {
  overflow: hidden;
  margin: auto;
  display: table;
}

.appointmentcalendar3 .calendar th h1 {
  font-weight: bold;
  font-size: 20px;
  text-align: left;
  color: #185875;
}

.appointmentcalendar3 .calendar td {
  font-weight: normal;
  font-size: 15px;
  -webkit-box-shadow: 0 2px 2px -2px #0E1119;
  -moz-box-shadow: 0 2px 2px -2px #0E1119;
  box-shadow: 0 2px 2px -2px #0E1119;
  font-weight: 800;
  color: black;
  width: 50px;
  height: 50px;
}

.appointmentcalendar3 .day{
  position: relative;
  width: 50px;
  height: 50px;
  border: 1px solid black;
}

.appointmentcalendar3 .day h3 {
  position: absolute;
  width: 50px;
  height: 50px;
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.appointmentcalendar3 .day h3:hover{
  cursor: pointer;
}

.appointmentcalendar3 .day .diagonal {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 50px;
  height: 50px;
}

.appointmentcalendar3 .day .bordertop {
  border-top: 1px solid black;
}

.appointmentcalendar3 .day .diagonal .line{
  stroke:black;
  stroke-width: 1;
}

.appointmentcalendar3 .day .day_first {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 50px;
  height: 25px;
}

.appointmentcalendar3 .day .day_second {
  position: absolute;
  bottom: 0px;
  left: 0px;
  width: 50px;
  height: 25px;
}

.appointmentcalendar3 .day .clickday {
  position: absolute;
  left: 0px;
  top: 0px;
  width: 50px;
  height: 50px;
  background: rgba(255,255,0, 0.6);
}

.appointmentcalendar3 .day .bold {
  font-size: 25px;
  font-weight: 900;
  line-height: 20px;
  color: lightcoral;
}

.appointmentcalendar3 .day .triangleleft {
  position: absolute;
  left: 0px;
  top: 0px;
  width: 0px;
  height: 0px;
  border-top: 25px solid transparent;
  border-bottom: 25px solid transparent;
  border-left: 15px solid rgba(0, 255, 234, 0.7);
}


.appointmentcalendar3 .day .triangleright {
  position: absolute;
  right: 0px;
  top: 0px;
  width: 0px;
  height: 0px;
  border-top: 25px solid transparent;
  border-bottom: 25px solid transparent;
  border-right: 15px solid rgba(0, 255, 234, 0.7);
}

.appointmentcalendar3 .day .blue {
  background-color: rgb(0,100,255);
}

.appointmentcalendar3 .day .green {
  background-color: green;
}

.appointmentcalendar3 .day .red {
  background-color: red;
}

.appointmentcalendar3 .day .orange {
  background-color: orange;
}

.appointmentcalendar3 .calendar td,.appointmentcalendar3 .calendar th {
  text-align: center;
  width: 50px;
}

.appointmentcalendar3 .calendar th h1 {
  text-align: center;
  color: white;
}

/* Background-color of the odd rows */
.appointmentcalendar3 .calendar tr:nth-child(odd) {
	  background-color: rgba(222,222,222,0.7);
}

/* Background-color of the even rows */
.appointmentcalendar3 .calendar tr:nth-child(even) {
	  background-color: rgba(200,200,200,0.7);
}

.appointmentcalendar3 .calendar th {
	  background-color: #1F2739;
}

.appointmentcalendar3 .calendar th:first-child h1 { color: #FB667A; }
.appointmentcalendar3 .calendar td:first-child { color: #FB667A; }

.appointmentcalendar3 .calendar th:last-child h1 { color: #FB667A; }
.appointmentcalendar3 .calendar td:last-child { color: #FB667A; }

